.wrapper {
  text-align: center;
  width: 80%;
  border: 2px solid #000;
  border-radius: 10px;
  margin: 50px auto 0;
  padding: 20px;
  box-shadow: 5px 0 5px #000;
}

.breed-select {
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.cat-description {
  max-width: 60%;
  margin: 10px auto;
}

.loader {
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  border: 5px solid #450f0f;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  margin: 20px auto;
  animation: rotation 1s linear infinite;
  display: block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

